import { parse } from "flatted/esm";
import pako from "pako";

export const spliceUtil = function (object, item) {
  if (object.indexOf(item) === -1) return;
  object.splice(object.indexOf(item), 1);
};

const inflate = (data) => parse(pako.inflate(atob(data), { to: "string" }));

export const saveHash = (data) => {
  if (process.env.NODE_ENV && process.env.NODE_ENV !== "production") {
    console.log(data.data);
  }

  return new Promise((resolve, reject) => {
    return fetch("https://api.jsonbin.io/v3/b", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "X-Master-Key": "$2a$10$vE2WO4aJ8cbY/1olevzi9up4OOSaxwmd57SArLWg8OuaUkNvlFgZa"
      },
      body: JSON.stringify(data), // body data type must match "Content-Type" header
    })
      .then((response) => response.json())
      .then(function (response) {
        // Check if response contains the expected metadata
        if (response.metadata && response.metadata.id) {
          // Get the bin ID from the metadata
          const binId = response.metadata.id;
          // Encode the bin ID in base64 and return it
          return resolve(encodeURIComponent(btoa(binId)));
        }
        throw new Error("Invalid save");
      })
      .catch(function (error) {
        console.log("Fetch error:", error.message);
        reject(error);
      });
  });
};

const data =
  "";

export const loadHash = () => {
  return new Promise((resolve, reject) => {
    if (window.location.hash) {
      let location = atob(decodeURIComponent(window.location.hash.substring(1)));
      return fetch(
          "https://api.jsonbin.io/v3/b/" + location,
          {
            method: "GET",
            headers: {
              "X-Master-Key": "$2a$10$vE2WO4aJ8cbY/1olevzi9up4OOSaxwmd57SArLWg8OuaUkNvlFgZa"
            },
          },
      )
          .then((response) => response.json())
          .then(function (response) {
            resolve(inflate(response.record.data))
          })
          .catch(function (error) {
            console.log("Fetch error:", error.message);
            resolve(inflate(data));
          });
    } else {
      try {
        resolve(inflate(data));
      } catch (err) {
        console.error("Decompression error:", err.message);
        reject("Failed to decompress fallback data.");
      }
    }
  });
};

export const rebuildQueryParams = () => {
  const vars = {};
  window.location.href.replace(
    /[?&]+([^=&]+)=([^&]*)/gi,
    function (m, key, value) {
      vars[key] = value;
    },
  );
  if (Object.keys(vars).length) {
    return (
      "?" +
      Object.keys(vars)
        .map((k) => k + "=" + vars[k])
        .join("&")
    );
  }

  return "";
};

const enabledExperimentalFeatures = ["stg", "opt"];

export const useExperimentalFeature = (featureName) => {
  const vars = {};
  window.location.href.replace(
    /[?&]+([^=&]+)=([^&]*)/gi,
    function (m, key, value) {
      vars[key] = value;
    },
  );
  vars.useExperimentalFeatures = vars.useExperimentalFeatures || "";
  return (
    (vars.useExperimentalFeatures || "").split(",").includes(featureName) ||
    vars.useExperimentalFeatures === "all" ||
    enabledExperimentalFeatures.includes(featureName)
  );
};
