import React, { Component } from "react";
import { svgKeyDown, svgKeyUp } from "./keyboardEvents";
import {
  analyzeGraph,
  deselect_path_and_nodes,
  initSimulation,
  updateGraph,
  zoom_actions,
} from "./graphActions";
import { appendMarkerAttributes } from "./markerActions";

import * as d3 from "d3";
import { add_node, updateAllOverclockCalculations } from "./nodeActions";
import { parse, stringify } from "flatted/esm";
import pako from "pako";
import { saveHash } from "./util";
import { withStyles } from "@mui/styles";

const styles = {
  tooltip: {
    position: "absolute",
    textAlign: "center",
    padding: 2,
    font: "12px sans-serif",
    background: "lightsteelblue",
    border: 0,
    borderRadius: 8,
    pointerEvents: "none",
    zIndex: 1202,
  },
};

class GraphSvg extends Component {
  constructor(props) {
    super(props);
    this.state = {
      snapToGrid: true,
    };
  }

  static inflateGraphData(data) {
    return parse(pako.inflate(atob(data), { to: "string" }));
  }

  addNode(nodeData) {
    add_node(nodeData, this);

    console.log(JSON.stringify(nodeData));
  }

  resetCamera() {
    this.inputSvg
      .transition()
      .duration(750)
      .call(this.zoom_handler.transform, d3.zoomIdentity);

    this.updateGraphHelper();
  }

  jiggle() {
    this.graphData.nodes.forEach((node) => {
      node.x = 0;
      node.y = 0;
    });

    this.updateGraphHelper();
  }

  fixNodes() {
    this.graphData.nodes.forEach((node) => {
      node.fx = node.x;
      node.fy = node.y;
    });

    this.updateGraphHelper();
  }

  unfixNodes() {
    this.graphData.nodes.forEach((node) => {
      node.fx = null;
      node.fy = null;
      node.vx = 0;
      node.vy = 0;
    });

    this.updateGraphHelper();
  }

  createGraph(inputSvg, nodes = [], links = [], data = {}) {
    this.graphData = {
      nodes: nodes,
      links: links,
    };

    this.id = Math.max(...this.graphData.nodes.map((elem) => elem.id)) + 1;
    if (this.id === Number.NEGATIVE_INFINITY) {
      this.id = 0;
    }
    this.inputSvg = inputSvg;

    //add encompassing group for the zoom
    this.svgGroup = inputSvg
      .append("g")
      .attr("class", "objects")
      .attr("id", "svgGroup");

    const graphObjects = this.svgGroup;

    const t = this;

    inputSvg.on("click", function (d) {
      deselect_path_and_nodes.call(this, t);
    });

    d3.select(window)
      .on("keydown", function (d) {
        svgKeyDown.call(this, d, t);
      })
      .on("keyup", function (d) {
        svgKeyUp.call(this, d, t);
      });

    //add zoom capabilities
    this.zoom_handler = d3
      .zoom()
      .on("zoom", () => zoom_actions(graphObjects))
      .scaleExtent([0.1, 6]);
    this.zoom_handler(inputSvg);
    inputSvg.on("dblclick.zoom", null);

    //Create definitions for the arrow markers showing relationship directions
    const defs = graphObjects.append("defs");
    appendMarkerAttributes(
      defs
        .append("svg:marker")
        .attr("id", "default-path-arrow")
        .attr("refX", 35),
    );

    appendMarkerAttributes(
      defs
        .append("svg:marker")
        .attr("id", "highlight-path-arrow-orange")
        .attr("fill", "orange")
        .attr("refX", 24),
    );

    appendMarkerAttributes(
      defs.append("svg:marker").attr("id", "dragged-end-arrow").attr("refX", 7),
    );

    const filter = defs
      .append("filter")
      .attr("id", "drop-shadow")
      .attr("height", "130%")
      .attr("width", "130%")
      .attr("filterUnits", "userSpaceOnUse");

    filter
      .append("feGaussianBlur")
      .attr("in", "SourceAlpha")
      .attr("stdDeviation", 5)
      .attr("result", "blur");

    filter.append("feOffset").attr("in", "blur").attr("result", "offsetBlur");

    filter
      .append("feFlood")
      .attr("in", "offsetBlur")
      .attr("flood-color", "white")
      .attr("flood-opacity", "1")
      .attr("result", "offsetColor");

    filter
      .append("feComposite")
      .attr("in", "offsetColor")
      .attr("in2", "offsetBlur")
      .attr("operator", "in")
      .attr("result", "offsetBlur");

    const feMerge = filter.append("feMerge");

    feMerge.append("feMergeNode").attr("in", "offsetBlur");
    feMerge.append("feMergeNode").attr("in", "SourceGraphic");

    //The dragged line
    this.dragLine = graphObjects
      .append("g")
      .append("svg:path")
      .attr("class", "link dragline line-object hidden")
      .attr("d", "M0,0L0,0")
      .attr("stroke", function (d) {
        return d3.color("#000000");
      })
      .style("marker-end", "url(#dragged-end-arrow)");

    const graphLinksGroup = graphObjects
      .append("g") //graphLinksData
      .attr("class", "links-g-group");

    const graphNodesGroup = graphObjects
      .append("g")
      .attr("class", "nodes-g-group");

    let simulation = initSimulation();

    this.graphNodesGroup = graphNodesGroup;
    this.graphLinksGroup = graphLinksGroup;
    this.simulation = simulation;
    this.updateGraphHelper();
  }

  updateGraphHelper() {
    updateGraph.call(
      this,
      this.simulation,
      this.graphNodesGroup,
      this.graphLinksGroup,
    );
  }

  clearGraphDataRaw() {
    const parent = d3.select(d3.select("#mainRender").node().parentElement);
    d3.select("#mainRender").selectAll("*").remove();
    d3.select("#mainRender").remove();

    return parent.append("svg").attr("id", "mainRender");
  }

  clearGraphData(t) {
    deselect_path_and_nodes.call(this, t);
    const svg = this.clearGraphDataRaw();
    this.createGraph(svg);
  }

  loadGraphData(data) {
    const svg = this.clearGraphDataRaw();
    //nodes, links, data
    this.createGraph(svg, data.graphData.nodes, data.graphData.links);
  }

  compressGraphData() {
    const compressedData = {
      satisgraphtory: {
        version: 0.01,
      },
      graphData: this.graphData,
      playerData: {},
      secret: {},
    };
    return btoa(
      pako.deflate(stringify(compressedData), { to: "string" }),
    );
  }

  uploadDataWithResponse() {
    const data = { data: this.compressGraphData() };
    return saveHash(data);
  }

  analyze = () => {
    analyzeGraph.call(this);
    this.updateGraphHelper();
  };

  optimize = () => {
    analyzeGraph.call(this, true);
    updateAllOverclockCalculations();
    this.updateGraphHelper();
  };

  componentDidMount() {
    // // const data = '';
    // const data = '';
    // // const data = '';
    // // const data = '';
    // // const data = '';
    this.loadGraphData(this.props.parentAccessor.state.coreGraphData);
    this.analyze();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (
      this.state.selectedPath !== prevState.selectedPath ||
      this.state.selectedNode !== prevState.selectedNode
    ) {
      this.props.parentAccessor.setState({
        selectedPath: this.state.selectedPath,
        selectedNode: this.state.selectedNode,
      });
    }
  }

  render() {
    return <svg id="mainRender" />;
  }
}

export default withStyles(styles)(GraphSvg);
